.sch-header h1 {
  color: white;
}

.sch-header h1 span {
  color: #85d22b;
  font-weight: 700;
}

.sch-header iframe {
  width: 100%;
  height: 340px;
  margin-top: 10px;
  margin-bottom: 10px;
}

.sch-form-head-text {
  color: white;
  font-weight: 700;
}

.sch-form-label {
  color: white;
  margin-bottom: 0px;
}

.schedule-call-form {
  padding: 15px 0;
  background-color: #cfa73891;
  border-radius: 10px;
}

.schedule-call-submit {
  background-color: #07cc27;
  border-color: #07cc27;
  color: white;
}

.sch-hr {
  background: var(--theme-secondary);
  content: "";
  display: block;
  height: 4px !important;
  margin: auto;
  width: 100px;
  opacity: 1;
}

.sch-side-image {
  width: 100%;
  max-width: 400px;
}

.side-img-text-div h1 {
  margin-bottom: 20px;
}

.side-img-text-div h1 span {
  font-weight: 700;
}

.side-img-text-div p {
  font-size: 20px;
}

.sch-btn-a {
  font-size: 25px;
  font-weight: 600;
  background-color: #ffae00;
  border-radius: 50px 50px 50px 50px;
  padding: 15px 50px;
  color: white;
  text-decoration: none;
}

.btn-call-head {
  margin-bottom: 20px;
}

.agent-bio {
  margin-bottom: 30px;
}
